.preloader {
  position: fixed;
  display: block;
  width: 100%;
  margin: 0 auto;
  padding-top: 80px;
  padding-bottom: 80px;
  background-image: url("../img/bg/bg-start@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow-y: auto;

  &--hidden {
    display: none;
  }

  @include retina {
    background-image: url("../img/bg/bg-start@2x.jpg");
  }

  @media (max-width: 1200px) {
    padding-left: 4.884%;
  }

  @include viewport--sm {
    height: 100vh;
    padding: 24px;
    background-image: url("../img/bg/bg-mobile-start@1x.jpg");
    background-position: center bottom;

    @include retina {
      background-image: url("../img/bg/bg-mobile-start@2x.jpg");
    }
  }
}

.preloader__container {
  display: flex;
  justify-content: space-between;
  max-width: 934px;
  height: calc(100vh - 78px);
  margin: 0 auto;
  padding-left: 56px;
  padding-right: 20px;

  @include viewport--sm {
    max-width: none;
    padding: 0;
  }
}

.preloader__col-1 {
  margin-right: 38px;

  @include viewport--sm {
    min-height: 667px;
    margin-right: 0;
  }
}

.preloader a {
  display: block;
  margin-top: 12px;
  margin-bottom: 205px;

  &:last-child {
    margin-bottom: 0;
  }

  @include viewport--sm {
    margin-top: 0;
    margin-bottom: 379px;
  }
}

.preloader img {
  width: 175px;
  height: 94px;

  @include viewport--sm {
    width: 131px;
    height: 75px;
  }
}

.preloader__button {
  display: inline-block;
  width: max-content;
  margin: 0 0 7px 1px;
  padding: 15px 41px;
  font-size: 25px;
  line-height: 25px;
  text-align: center;
  vertical-align: middle;
  background: $color-tahiti-gold;
  color: $color-default-white;
  border: 0;
  border-radius: 45px;
  letter-spacing: 0.05em;
  transition: background $default-transition-settings,
    box-shadow $default-transition-settings;
  cursor: pointer;
  outline: none;

  &:hover,
  &:focus {
    background: linear-gradient(180deg, rgba($color-default-white, 0.177) 0%, rgba($color-default-white, 0.24) 5.72%, rgba($color-default-white, 0) 73.21%), $color-tahiti-gold;
    box-shadow: 0 10px 15px rgba($color-default-black, 0.1);
  }

  &:active {
    background: $color-tahiti-gold;
    box-shadow: inset 0 0 5px rgba($color-default-black, 0.25);
  }

  @include viewport--sm {
    margin: 0;
    padding: 12px 52px 11px;
    font-size: 20px;
    line-height: 25px;
  }
}

.preloader__col-2 {
  max-width: 600px;
  height: auto;
  margin-bottom: 80px;
  overflow-y: auto;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  @include viewport--sm {
    position: absolute;
    top: 105px;
    max-width: 327px;
    width: 327px;
    height: 341px;
    overflow: auto;
  }
}

.preloader h1 {
  margin: 0;
  margin-bottom: 41px;
  font-weight: 900;
  font-size: 90px;
  line-height: 90px;
  color: $color-tahiti-gold;

  @include viewport--sm {
    margin-bottom: 20px;
    font-size: 48px;
    line-height: 48px;
  }
}

.preloader p {
  margin: 0;
  margin-bottom: 27px;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.05em;

  @include viewport--sm {
    margin-bottom: 14px;
    font-size: 16px;
    line-height: 24px;
  }
}
