.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-matterhorn, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include viewport--sm {
      // padding: 60px 16px;
      width: 375px;
      padding: 0;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;

    background-color: $color-default-white;

    @include viewport--sm {
      width: 100%;
      // padding: 20px;
      padding: 0;
    }
  }

  &__close-btn {
    position: absolute;
    top: 21px;
    right: 30px;
    z-index: 1;
    display: block;
    width: 15px;
    height: 15px;

    &:active,
    &:focus {
      outline: none;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: -12px;
      left: -12px;
      padding: 20px;
    }

    svg {
      stroke: $color-silver;
      stroke-width: 3px;

      @include viewport--sm {
        stroke: $color-default-white;
        stroke-width: 1.5px;
      }
    }

    &:hover svg,
    &:focus svg {
      stroke: $color-tahiti-gold;

      @include viewport--sm {
        stroke: $color-default-white;
      }
    }

    // @include viewport--sm {
    //   position: fixed;
    //   top: 40px;
    //   right: 24px;
    //   z-index: 2;
    //   // display: none;
    //   width: 40px;
    //   height: 40px;
    //   border: 1.5px solid $color-default-white;
    //   border-radius: 50%;

    //   &::before {
    //     display: none;
    //   }
    // }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include viewport--sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}


.modal__content > .modal__close-btn {
  @include viewport--sm {
    display: none;
  }
}
